.input-default{
    width: 100%;
    height: 25px;
    border-bottom: 1px solid $white;
    color: $white;
    &::placeholder{
        color: rgba($white, 0.5) ;
    }
}

.container-input{
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding-top: 40px;
}

.select-rol{
    position: relative;
    width: 100%;
    height: 25px;
    border-bottom: 1px solid $white;
    color: $white;
    &::placeholder{
        color: rgba($white, 0.5) ;
    }
    &.selectize-control{
        .selectize-input{
            &::after{
                content: '';
                transform-origin: center;
                position: absolute;
                top: calc(50% - 2px);
                right: 0;
                transform: translateY(-50%);
                width: 14px;
                height: 8px;
                background-image: url("data:image/svg+xml,%3Csvg width='14' height='8' viewBox='0 0 14 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.00293 0.5L7.24157 6.5L13.4802 0.5' stroke='%23F7F6F3'/%3E%3C/svg%3E%0A");
            }
            &.dropdown-active{
                &::after{
                    transform: translateY(-50%) rotate(180deg);
                }
            }
        }
        
        
    }
    
}

.selectize-dropdown{
    position: absolute;
    top: calc(100% + 10px)  !important;
    left: 0 !important;
    .selectize-dropdown-content{
        background-color: $lightbrown;
        border: 1px solid $white;
        display: flex;
        flex-direction: column;
        padding: 10px 10px 5px;
        .option{
            cursor: pointer;
            height: 25px;
            vertical-align: middle;
        }
    }
}

.selectize-input{
    cursor: pointer;
    input{
        width: 100%;
        color: $white;
        cursor: pointer;
        caret-color: transparent;
        &::placeholder{
            color: rgba($white, 0.5) ;
        }
    }
}